// src/pages/Home.js

import React, { useMemo, useState, useEffect, useRef } from 'react';
import './Slider.css';
import { Helmet } from 'react-helmet';
import Select from 'react-select';
import Slider from 'rc-slider';
import 'rc-slider/assets/index.css';
import Buildings_component from '../components/Buildings_component';
import Loading from '../components/Loading';

import promo1 from '../assets/images/img/promo1.png';
import promo2 from '../assets/images/img/promo2.png';
import promo3 from '../assets/images/img/promo3.png';
import promo4 from '../assets/images/img/promo4.jpg';
import background1 from '../assets/images/img/background.png';

const Home = () => {
    const [area, setArea] = useState([18.75, 141.88]);
    const [cost, setCost] = useState([600, 2000]);
    const [floors, setFloors] = useState([1, 32]);

    //    
    const customSelectStyles = useMemo(() => ({
        control: (provided, state) => ({
            ...provided,
            width: '100%',
            height: '50px',
            fontSize: '14px',
            borderRadius: '8px',
            borderColor: state.isFocused ? '#10554b' : '#D5D7DA',
            boxShadow: state.isFocused ? '0 0 0 0px #10554b' : 'none',
            color: '#abaaaa',
            '&:hover': { borderColor: '#10554b' },
        }),
        option: (provided, state) => ({
            ...provided,
            width: '98%',
            margin: '0 auto 1%',
            fontSize: '16px',
            borderRadius: '8px',
            color: state.isSelected ? 'white' : 'gray',
            backgroundColor: state.isSelected ? '#10554b' : state.isFocused ? '#e8f4f2' : 'white',
            '&:hover': { backgroundColor: '#e8f4f2' },
        }),
        singleValue: (provided) => ({
            ...provided,
            fontSize: '14px',
            color: 'gray',
        }),
        placeholder: (provided) => ({
            ...provided,
            color: '#abaaaa',
        }),
        menu: (provided) => ({
            ...provided,
            zIndex: 100,  //  z-index
        }),
    }), []);

    const selectOptions = [
        { value: 'all', label: ' ' },
        { value: 'project1', label: ' 1' },
        { value: 'project2', label: ' 2' },
    ];

    return (
        <div className="pages-content">
            <Helmet>
                <title> -   Tojsokhtmon</title>
                <meta name="description" content="       ." />
                <meta name="keywords" content=", ,  " />
            </Helmet>
            <header>
                <div className="promotion">
                    <div className='promotion-description'>
                        <div className='promotion-description-content'>
                            <h2>  " "</h2>
                            <p>    iPhone 16 Pro  !</p>
                            <a href='#'></a>
                        </div>
                    </div>
                    <div className='promotion-img'>
                        <img src={promo4} alt='Promo1' />
                    </div>
                </div>
                <section>
                    <div className='section-title'></div>
                    <div className='section-description'>       <span>  </span></div>
                    <a href='#'>  <i class="fa fa-chevron-right" aria-hidden="true"></i></a>
                </section>
            </header>

            <main>
                <Buildings_component />
                <div className='home-filter-apartments'>
                    <section className='sec2'>
                        <div className='section-title'><br /> </div>
                        <div className='section-description s2'>  <br /> </div>
                        <div className='section-two-flex'>
                            <div className='section-content'>         20  </div>
                        </div>
                    </section>

                    <div className='filter-home-apartments'>
                        <div className='filter-home-apartments-background'>
                            <img src={background1} alt='' />
                        </div>
                        <div className='filter-home-apartments-form'>
                            <div className='filter-home-select-slider'>
                                <div className='filter-home-apartments-select'>
                                    {/* Selects */}
                                    <div className='filter-home-select'>
                                        <label> </label>
                                        <Select
                                            options={selectOptions}
                                            styles={customSelectStyles}
                                            placeholder=" "
                                        />
                                    </div>
                                    <div className='filter-home-select'>
                                        <label> </label>
                                        <Select
                                            options={[{ value: 'all', label: '' }, { value: '1', label: '1 ' }, { value: '2', label: '2 ' }]}
                                            styles={customSelectStyles}
                                            placeholder=""
                                        />
                                    </div>
                                    <div className='filter-home-select fh3'>
                                        <label> </label>
                                        <Select
                                            options={[{ value: 'all', label: ' ' }, { value: '2023', label: '2023' }, { value: '2024', label: '2024' }]}
                                            styles={customSelectStyles}
                                            placeholder=" "
                                        />
                                    </div>
                                </div>
                                <div className='filter-home-apartments-slider'>
                                    {/* Sliders */}
                                    <div className='filter-home-slider' >
                                        <label>18.75 - 141.88</label>
                                        <Slider
                                            range
                                            min={18.75}
                                            max={141.88}
                                            step={0.1}
                                            defaultValue={area}
                                            onChange={(value) => setArea(value)}
                                            trackStyle={{ backgroundColor: '#10554b' }}
                                            handleStyle={{ borderColor: '#10554b' }}
                                            style={{width: '90%'}}
                                        />
                                        <div className='filter-home-slider-footer'>
                                            <span>{area[0]} </span>
                                            
                                            <span>{area[1]} </span>
                                        </div>
                                    </div>

                                    <div className='filter-home-slider' >
                                        <label>$600 - $2000</label>
                                        <Slider
                                            range
                                            min={600}
                                            max={2000}
                                            step={50}
                                            defaultValue={cost}
                                            onChange={(value) => setCost(value)}
                                            trackStyle={{ backgroundColor: '#10554b' }}
                                            handleStyle={{ borderColor: '#10554b' }}
                                            style={{width: '90%'}}
                                        />
                                        <div className='filter-home-slider-footer'>
                                            <span>{cost[0]} $</span>
                                            
                                            <span>{cost[1]} $</span>
                                        </div>
                                    </div>

                                    <div className='filter-home-slider fh3' >
                                        <label>1 - 32 </label>
                                        <Slider
                                            range
                                            min={1}
                                            max={32}
                                            step={1}
                                            defaultValue={floors}
                                            onChange={(value) => setFloors(value)}
                                            trackStyle={{ backgroundColor: '#10554b' }}
                                            handleStyle={{ borderColor: '#10554b' }}
                                            style={{width: '90%'}}
                                        />
                                        <div className='filter-home-slider-footer'>
                                            <span>{floors[0]} </span>
                                            
                                            <span>{floors[1]} </span>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <a href='#'>1830  <span> <i class="fa fa-chevron-right" aria-hidden="true"></i></span></a>
                        </div>
                    </div>
                </div>
            </main>

            <footer>
                <p> </p>
            </footer>
        </div>
    );
};

export default Home;
