import React, { useState, useCallback, useMemo } from 'react';
import Select from 'react-select';
import './Buildings_component.css';
import cart1 from '../assets/images/img/cart1.jpg';
import cart2 from '../assets/images/img/cart2.PNG';
import cart3 from '../assets/images/img/cart3.PNG';
import cart4 from '../assets/images/img/cart4.JPG';
import cart5 from '../assets/images/img/cart5.JPG';
import cart7 from '../assets/images/img/cart6.PNG';
import cart6 from '../assets/images/img/cart7.PNG';
import cart8 from '../assets/images/img/cart8.PNG';
import Loading from './Loading';

const LazyImage = ({ src, alt }) => {
    const [isLoaded, setIsLoaded] = useState(false);
  
    return (
      <>
        <img
          className={`lazy-img ${isLoaded ? 'loaded' : ''}`}
          src={src}
          alt={alt}
          loading="lazy"
          onLoad={() => setIsLoaded(true)}
        />
      </>
    );
  };

//    
const Cart = React.memo(({ image, tags, title, description, apartmentsCount, price }) => {
    return (
      <div className='cart'>
        <div className='cart-header'>
          <LazyImage src={image} alt={title} />
          <div className='cart-tags'>
            {tags.map(tag => (
              <div key={tag} className='cart-tag'>
                <i className='fa fa-building'></i>{tag}
              </div>
            ))}
          </div>
        </div>
        <div className='cart-footer'>
          <div className='title-description'>
            <h2>{title}</h2>
            <p>{description}</p>
          </div>
          <div className='apartments-price'>
            <p>
              <span>{apartmentsCount} </span>
              <span> ${price}</span>
            </p>
          </div>
        </div>
      </div>
    );
  });

const Buildings_component = () => {
    //    
    const [selectedTags, setSelectedTags] = useState([]);
    
    //   
    const objectOptions = useMemo(() => [
        { value: 'all', label: ' ' },
        { value: 'apartments', label: '' },
        { value: 'houses', label: '' },
    ], []);

    const deadlineOptions = useMemo(() => [
        { value: 'any', label: '' },
        { value: '2024', label: '2024' },
        { value: '2025', label: '2025' },
    ], []);

    const tags = useMemo(() => [
        { id: 'keys', label: ' ', iconClass: 'fa fa-key' },
        { id: 'economy', label: '', iconClass: 'fa fa-building' },
        { id: 'comfort', label: '', iconClass: 'fa fa-building' },
        { id: 'business', label: '', iconClass: 'fa fa-building' },
        { id: 'white-box', label: 'White box', iconClass: 'fa fa-paint-brush' },
        { id: 'plaster', label: '', iconClass: 'fa fa-paint-brush' },
    ], []);

    //   
    const handleTagChange = useCallback((tagId) => {
        setSelectedTags(prevSelectedTags =>
            prevSelectedTags.includes(tagId)
                ? prevSelectedTags.filter(id => id !== tagId)
                : [...prevSelectedTags, tagId]
        );
    }, []);

    //    
    const customSelectStyles = useMemo(() => ({
        control: (provided, state) => ({
            ...provided,
            height: '50px',
            fontSize: '14px',
            borderRadius: '8px',
            borderColor: state.isFocused ? '#10554b' : '#D5D7DA',
            boxShadow: state.isFocused ? '0 0 0 0px #10554b' : 'none',
            color: '#abaaaa',
            '&:hover': { borderColor: '#10554b' },
        }),
        option: (provided, state) => ({
            ...provided,
            width: '98%',
            margin: '0 auto 1%',
            fontSize: '16px',
            borderRadius: '8px',
            color: state.isSelected ? 'white' : 'gray',
            backgroundColor: state.isSelected ? '#10554b' : state.isFocused ? '#e8f4f2' : 'white',
            '&:hover': { backgroundColor: '#e8f4f2' },
        }),
        singleValue: (provided) => ({
            ...provided,
            fontSize: '14px',
            color: 'gray',
        }),
        placeholder: (provided) => ({
            ...provided,
            color: '#abaaaa',
        }),
    }), []);

    //   
    const projects = useMemo(() => [
        { image: cart1, tags: ['', ''], title: '', description: '17 ', apartmentsCount: 364, price: 700 },
        { image: cart2, tags: ['', ''], title: '', description: '.   117,   Waikiki', apartmentsCount: 374, price: 800 },
        { image: cart3, tags: ['', '', ' '], title: '', description: '. .      ', apartmentsCount: 409, price: 700 },
        { image: cart4, tags: ['', ''], title: '', description: '. ', apartmentsCount: 76, price: 400 },
        { image: cart5, tags: ['', ''], title: '  - 2', description: '13 ', apartmentsCount: 40, price: 600 },
        { image: cart7, tags: ['', ''], title: ' 12  - 2', description: '12 ', apartmentsCount: 12, price: 450 },
        { image: cart6, tags: ['', ''], title: ' ', description: '. ,  ', apartmentsCount: 140, price: 450 },
        { image: cart8, tags: ['', ''], title: ' 8 ', description: '8 ', apartmentsCount: 288, price: 400 },
    ], []);

    return (
        <div className='buildings'>
            <div className="filter">
                <div className='filter-form'>
                    <div className='filter-1str'>
                        <div className="filter-group">
                            <label> </label>
                            <Select
                                options={objectOptions}
                                styles={customSelectStyles}
                                className="custom-select"
                                placeholder=" "
                            />
                        </div>
                        <div className="filter-group">
                            <label> </label>
                            <Select
                                options={deadlineOptions}
                                styles={customSelectStyles}
                                className="custom-select"
                                placeholder=""
                            />
                        </div>
                    </div>
                    <div className='filter-2str'>
                        <div className="tags">
                            {tags.map(tag => (
                                <label key={tag.id} className={`tag ${selectedTags.includes(tag.id) ? 'active' : ''}`}>
                                    <input
                                        type="checkbox"
                                        checked={selectedTags.includes(tag.id)}
                                        onChange={() => handleTagChange(tag.id)}
                                    />
                                    <i className={tag.iconClass} style={{ marginRight: '8px' }}></i>
                                    {tag.label}
                                </label>
                            ))}
                        </div>
                        <button className="submit-button"></button>
                    </div>
                </div>
                <div className='filter-result-count'>
                    <h2>25<br /></h2>
                </div>
            </div>

            <div className='carts'>
                {projects.map(project => (
                    <Cart
                        key={project.title}
                        image={project.image}
                        tags={project.tags}
                        title={project.title}
                        description={project.description}
                        apartmentsCount={project.apartmentsCount}
                        price={project.price}
                    />
                ))}
            </div>
        </div>
    );
};

export default Buildings_component;

